import React from "react";
import Chart from "react-apexcharts";

function sumOfArrayElements(n) {
  let myarray = [...new Array(n+1)].map((item, index) => { return 10 / Math.pow(1.1, index + 1) });
//  console.log(n);
  return Math.round(myarray.reduce((a, b) => a + b, 0) * 100) / 100
}

